// next-app/components/PostCard.js
import Link from 'next/link';
import { formatDistanceToNow } from 'date-fns';
import { useState, useEffect } from 'react';
import axios from '../services/api';

export default function PostCard({ post: initialPost }) { // Renamed prop to initialPost for internal state management
  const [post, setPost] = useState(initialPost); // Manage the post object in local state
  const [currentUserId, setCurrentUserId] = useState(null); // To store logged-in user's ID

  // This useEffect synchronizes local state with prop changes and identifies the current user's vote
  useEffect(() => {
    setPost(initialPost); // Always sync with the prop if it changes

    const storedUser = localStorage.getItem('user');
    if (storedUser) {
      try {
        setCurrentUserId(JSON.parse(storedUser)._id);
      } catch (e) {
        console.error("Failed to parse user from localStorage:", e);
        setCurrentUserId(null);
      }
    }
  }, [initialPost]); // Rerun when the initialPost prop changes

  // Derived state for easier use in JSX
  const voteCount = post.upvotes.length - post.downvotes.length;
  const hasUpvoted = currentUserId && post.upvotes.includes(currentUserId);
  const hasDownvoted = currentUserId && post.downvotes.includes(currentUserId);

  const handleVote = async (voteType) => { // voteType will be 'upvote' or 'downvote'
    const token = localStorage.getItem('token');
    if (!token) {
      alert('Please log in to vote.');
      return;
    }

    try {
      // Backend expects POST to /posts/:id/upvote or /posts/:id/downvote
      const endpoint = `/posts/${post._id}/${voteType}`;
      const res = await axios.post(endpoint, {}, { // No body needed for these endpoints
        headers: { Authorization: `Bearer ${token}` },
      });

      // Update the local post state with the response from the backend
      // The backend sends the full updated post object
      setPost(res.data);

    } catch (error) {
      console.error('Error voting:', error.response?.data || error.message);
      alert('Failed to cast vote. Please try again.');
    }
  };

  return (
    <div className="flex bg-white rounded-lg shadow-md hover:shadow-lg transition-shadow duration-300 ease-in-out mb-4 border border-gray-200">
      {/* Vote Column */}
      <div className="flex flex-col items-center p-2 bg-gray-50 rounded-l-lg border-r border-gray-100">
        <button
          onClick={() => handleVote('upvote')} // Call with 'upvote' type
          className={`text-gray-500 hover:text-orange-500 ${hasUpvoted ? 'text-orange-500' : ''}`} // Use hasUpvoted for styling
          aria-label="Upvote"
        >
          <svg className="w-6 h-6" fill="currentColor" viewBox="0 0 24 24"><path d="M12 4l8 8H4l8-8z"/></svg>
        </button>
        <span className="font-bold text-md py-1 text-gray-800">{voteCount}</span>
        <button
          onClick={() => handleVote('downvote')} // Call with 'downvote' type
          className={`text-gray-500 hover:text-blue-500 ${hasDownvoted ? 'text-blue-500' : ''}`} // Use hasDownvoted for styling
          aria-label="Downvote"
        >
          <svg className="w-6 h-6" fill="currentColor" viewBox="0 0 24 24"><path d="M12 20l-8-8h16l-8 8z"/></svg>
        </button>
      </div>

      {/* Content Column */}
      <div className="flex-1 p-4">
        <div className="flex items-center text-sm text-gray-600 mb-2">
          <span className="font-semibold text-gray-700">Posted by u/{post.author?.username || 'Anonymous'}</span>
          <span className="mx-2">â€¢</span>
          <span>{formatDistanceToNow(new Date(post.createdAt), { addSuffix: true })}</span>
        </div>
        <Link href={`/post/${post._id}`} className="block">
          <h2 className="text-xl font-semibold text-gray-900 hover:text-blue-700 cursor-pointer mb-2 leading-tight">{post.title}</h2>
        </Link>
        <p className="text-gray-700 text-base line-clamp-3 mb-3">{post.contentMarkdown}</p>

        <div className="flex items-center text-sm text-gray-600">
          <Link href={`/post/${post._id}#comments`} className="flex items-center hover:text-blue-700">
            <svg className="w-5 h-5 mr-1" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M7 8h10M7 12h4m1 8l-4-4H5a2 2 0 01-2-2V6a2 2 0 012-2h14a2 2 0 012 2v8a2 2 0 01-2 2h-3l-4 4z"></path></svg>
            <span className="font-semibold">{post.comments?.length || 0} Comments</span> {/* Assuming comments array on post */}
          </Link>
        </div>
      </div>
    </div>
  );
}